.list {
    list-style-type: none;
    padding: 0;
    margin-bottom: 40px;
    max-height: 150px;
    overflow-y: scroll;
  }
  
  .list li {
    background-color: #fff;
    box-shadow: var(--box-shadow);
    color: #333;
    display: flex;
    justify-content: space-between;
    position: relative;
    padding: 10px;
    margin: 10px 0;
  }
  
  .list li.plus {
    border-right: 5px solid #2ecc71;
  }
  
  .list li.minus {
    border-right: 5px solid #c0392b;
  }

  .list li:hover .delete-btn {
    opacity: 1;
  }
  